import { AfterViewInit, Component, OnInit } from '@angular/core';
import { AuthService } from '../core/auth.service';
import { PetService } from '../core/pet.service';
import { UtilityService } from '../core/utility.service';
import { ActivatedRoute , Router} from '@angular/router';
import { ServiceProviderService } from '../core/service-provider.service';
import { DomSanitizer } from '@angular/platform-browser';
@Component({
  selector: 'app-edit-service',
  templateUrl: './edit-service.page.html',
  styleUrls: ['./edit-service.page.scss'],
})
export class EditServicePage implements OnInit {
	
	serviceDetail: any;
	id: any;
	images_dynamic: any = [];
	file = '';
	file1;
	description: any = '';
	model: any = {
	    startTime: '',
	    endTime: '',
	    sname: '',
	    experience: '1',
	    price: '',
	    description: '',
  	};
  	previewImage = null;
  	userId;

  	price = [
  		{
  			desc: '',
  			price:''
  		}
  	]
  	images = [];
  	previewimages = [];
    servicecat: any;

  	constructor(private petService: PetService,
    private utility: UtilityService,
    private auth: AuthService,private sanitizer: DomSanitizer,
    private activatedRoute: ActivatedRoute, private router: Router,private providerService: ServiceProviderService) { 

  		this.id = this.activatedRoute.snapshot.params.id;
  		console.log(this.id, this.activatedRoute);
  	}

  	ngOnInit() {
  	}

  	ionViewDidEnter(){

  		this.getServiceCategoriesList();
  	}

  	getServiceCategoriesList(){

      this.providerService.serviceCatList().subscribe(
      (res) => {
        console.log(res);
        this.servicecat = res;
        this.getServiceDetail();
        this.utility.dismiss();
      },
      (err) => {
        console.log(err);
        this.utility.dismiss();
        this.utility.showAlert('Something went wrong!');
      }
    );
    }



  	handleFileImage(e) {
    console.log('here', e, e.target.files);
    
    // this.images.push(e.target.files[0]);
    //  console.log('here', this.images);
    //  this.previewimages.push(this.sanitizer.bypassSecurityTrustUrl(
    //   URL.createObjectURL(e.target.files[0])));
    // // this.previewImage = this.sanitizer.bypassSecurityTrustUrl(
    // //   URL.createObjectURL(e.target.files[0])
    // // );




    this.images = e.target.files;

    const fileArray = Array.from(this.images);
    console.log(fileArray.length);
    const checkLength = fileArray.length + this.previewimages.length;
    if (checkLength > 4) {
      this.utility.showAlert('Max 4 images can be selected');

      return;
    }
    fileArray.forEach((v) => {
      this.previewimages.push(
        this.sanitizer.bypassSecurityTrustUrl(URL.createObjectURL(v))
      );
    });
  }

  	getServiceDetail(){

  		let dict = {

  			_id: this.id,
  			userId: JSON.parse(localStorage.getItem('userObj'))._id
  		}

    	this.auth.getServiceDetail(dict).subscribe(
        (res) => {
          console.log(res);
          this.serviceDetail = res;

          	this.model = {
			    startTime: this.serviceDetail.data.start_time,
			    endTime: this.serviceDetail.data.end_time,
			    sname: this.serviceDetail.data._id,
			    experience: '',
			    price: this.serviceDetail.data.visit_type,
			    description: this.serviceDetail.data.description,
		  	};
		  	this.images_dynamic = this.serviceDetail.data.image;
		  	if(this.serviceDetail.data.visit_type.length == 0){
		  		this.price.push({
		  			desc: '',
		  			price:''
		  		});
		  	}
		  	this.price = this.serviceDetail.data.visit_type;
          this.utility.dismiss();
        },
        (err) => {
          console.log(err);
          this.utility.dismiss();
          this.utility.showAlert('Something went wrong!');
        }
      );
  	}

  	submit(){

	 	if (this.model.sname == '') {
	      return this.utility.showAlert('Name is required');
	    }if (this.model.description == '') {
	      return this.utility.showAlert('Description is required');
	    }if (this.model.startTime == '') {
	      return this.utility.showAlert('Start time is required');
	    }if (this.model.endTime == '') {
	      return this.utility.showAlert('End time is required');
	    }if (this.price.length == 0) {
	      return this.utility.showAlert('Price is required');
	    }

	    // if (this.previewimages.length === 0 && this) {
	    //   return this.utility.showAlert('Atlease one image is required');
	    // }

	    this.utility.present();
	    const fd = new FormData();

	    Array.from(this.images).forEach((v: any) => {
	        fd.append('images', v);
	    });
	    // fd.append(
	    //   'video',
	    //   this.file1 != ''
	    //     ? this.file1
	    //     : JSON.parse(localStorage.getItem('data')).video
	    // );



	    var index = this.servicecat.findIndex(x => x._id === this.model.sname);
	    console.log('name ===', this.servicecat[index].name)

	    fd.append('description', this.model.description);
	    fd.append('start_time', this.model.startTime);
	    fd.append('end_time', this.model.endTime);
	    fd.append('experience', this.model.experience);
	    fd.append('visit_type', JSON.stringify(this.price));
	    fd.append('name', this.servicecat[index].name);
	    var id = JSON.parse(localStorage.getItem('userObj'))._id;
	    fd.append('userId', id);
	    fd.append('_id', this.id);
	    console.log(fd);

	    this.providerService.serviceUpdate(this.id, fd).subscribe(
	      (res) => {
	        console.log(res);
	        this.utility.dismiss();
	        this.utility.navigateRoot('/tabs/calender-appointment');
	      },
	      (err) => {
	        console.log(err);
	        this.utility.dismiss();
	        this.utility.showAlert('Something went wrong!');
	      }
	    );
  	}

}
